class Vue {
  constructor(option) {
    this.$date = option.data || {};
    this.$el =
      option.el === "string" ? document.querySelector(option.el) : option.el;

    option = Object.assign(
      {},
      {
        computed: {},
        methods: {}
      },
      option
    );

    this.$option = option;
  }
}

export default Vue;



